/// <reference types="@types/googlemaps" />
import { Component, OnInit } from '@angular/core';
import { Title } from "@angular/platform-browser";
import { Customer } from '../../models/customer';
import { CustomersService } from '../../services/customers.service';
import { Router } from '@angular/router';
import { MapsAPILoader } from '@agm/core';
import { ViewChild, ElementRef, NgZone } from '@angular/core';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.css']
})
export class AddCustomerComponent implements OnInit {

  pageTitle: string = 'Add Customer';
  headerIcon: string = 'fas fa-plus-circle';
  firstName: string = '';
  lastName: string = '';
  email: string = '';
  phone: string = '';
  address: string = '';
  notes: string = '';


  @ViewChild('search') public searchElement: ElementRef;

  constructor(
    private titleService: Title,
    private customersService: CustomersService,
    private router: Router,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
  ) { }

  ngOnInit(): void {
    this.titleService.setTitle('Comp CRM | Add Customer Form');

    this.mapsAPILoader.load().then(
      () => {
        let autocomplete = new google.maps.places.Autocomplete(this.searchElement.nativeElement, { types: ["address"] });

        autocomplete.addListener("place_changed", () => {
          this.ngZone.run(() => {
            let place: google.maps.places.PlaceResult = autocomplete.getPlace();
            if (place.geometry === undefined || place.geometry === null) {
              return;
            }
          });
        });
      }
    );
  }

  onSubmit({ value, valid }: { value: Customer, valid: boolean }): void {

    if (valid) {

      this.customersService.addCustomer(value);
      this.router.navigate(['/customers']);

    }

  }



}
