import { Component, OnInit } from '@angular/core';
import { Album } from '../../models/album';
import { Title } from "@angular/platform-browser";
import { AlbumsService } from '../../services/albums.service';

@Component({
  selector: 'app-albums',
  templateUrl: './albums.component.html',
  styleUrls: ['./albums.component.css']
})
export class AlbumsComponent implements OnInit {

  albums: Album[] = [];
  pageTitle: string = 'Albums';
  headerIcon: string = 'fas fa-images';

  constructor(private albumService: AlbumsService, private titleService: Title) { }

  ngOnInit(): void {

    this.titleService.setTitle('Comp CRM | Albums');
    this.albumService.getAlbums().subscribe((albums: Album[]) => {
      this.albums = albums;
    });

  }

}
